import React, { useState } from "react";
import Map from "./map_";
import "./mapViz.css";

export default function Liberdade() {
  const [selectedRua, setSelectedRua] = useState([]);
  const nomes = [
    "Liberdade",
    "25 de Abril",
    "Salgueiro Maia",
    "Capites de Abril",
  ];
  const colors = ["#f25f5c", "#247ba0", "#ff9f1c", "#70c1b3"];

  return (
    <div className="wrapper">
      <p
        style={{
          fontSize: "12px",
          fontStyle: "italic",
          paddingBottom: 0,
          marginBottom: 5,
          color: "grey",
        }}
      >
        Selecciona uma das expresses para veres onde esto as ruas com esse
        nome
      </p>
      {nomes.map((nome, index) => (
        <div style={{ display: "inline-block" }}>
          <div
            onClick={() =>
              setSelectedRua(
                selectedRua.includes(nome)
                  ? selectedRua.filter((v) => v !== nome)
                  : [...selectedRua, nome]
              )
            }
            style={{
              paddingRight: "10px",
              fontWeight: "bold",
              textDecoration: selectedRua.includes(nome) ? "underline" : null,
              color: colors[index],
              cursor: "pointer",
              textAlign: "center",
            }}
          >
            {" "}
            {nome}
          </div>
        </div>
      ))}
      <div id="map">
        <Map selectedRua={selectedRua} nomes={nomes} cor={colors}></Map>
      </div>
      <div style={{ textAlign: "center", fontSize: "14px" }}>
        <p style={{ fontWeight: "bold" }}>Nota metodolgica</p>
        <p style={{ marginRight: "20%", marginLeft: "20%", fontSize: "12px" }}>
          Os dados originais das ruas foram juntados (usando um spatial join -
          https://geopandas.org/en/stable/gallery/spatial_joins.html) com dados
          oficiais de shapefiles das freguesias e concelhos, disponvel no
          repositrio. Devido  ocorrncia de duplicados (casos em que a rua
          atravessa dois concelhos/freguesias), manteve-se a linha com o osm_id
          mais baixo resultante dessa operao.
        </p>
        <p style={{ marginRight: "20%", marginLeft: "20%", fontSize: "12px" }}>
          Os dados das ruas pertencentes ao mesmo concelho e freguesia e com o
          mesmo nome foram agregados na coluna unique_index e
          rua_freguesia_concelho. Pode haver um erro associado a esta operao,
          no caso da mesma freguesia ter duas ruas em locais distintos com o
          mesmo nome.
        </p>
        <p style={{ marginRight: "20%", marginLeft: "20%", fontSize: "10px" }}>
          Dados do Open Street Map (descarregados atravs do
          <a
            href="https://download.geofabrik.de/europe/portugal.html"
            target="_blank"
          >
            {" "}
            Geofabrik{" "}
          </a>
          )
        </p>
      </div>
    </div>
  );
}
