import {
  MapContainer,
  TileLayer,
  Polyline,
  Tooltip,
  Polygon,
} from "react-leaflet";
import { GeoSearchControl, OpenStreetMapProvider } from "leaflet-geosearch";
import data from "./assets/ruas_abril.json";
import "leaflet/dist/leaflet.css";
import SearchControl from "./searchControl";

export default function Map(props) {
  const provider = new OpenStreetMapProvider({
    // limitar pesquisa a um pas. Fonte https://stackoverflow.com/questions/54565672/how-to-restrict-the-search-of-a-place-by-the-code-of-the-country-using-geosearch
    params: {
      countrycodes: "pt",
    },
  });

  const heightofMap = window.screen.height;
  const data_features = data.features;
  const position = [39.6071, -9.13549];
  let styleObject;
  function style(string) {
    if (string.includes("Liberdade")) {
      styleObject = {
        color: "#f25f5c",
        opacity:
          props.selectedRua.length === 0
            ? 1
            : props.selectedRua.includes("Liberdade")
            ? 1
            : 0,
      };
    } else if (string.includes("25 de Abril")) {
      styleObject = {
        color: "#247ba0",
        opacity:
          props.selectedRua.length === 0
            ? 1
            : props.selectedRua.includes("25 de Abril")
            ? 1
            : 0,
      };
    } else if (string.includes("Salgueiro Maia")) {
      styleObject = {
        color: "#ff9f1c",
        opacity:
          props.selectedRua.length === 0
            ? 1
            : props.selectedRua.includes("Salgueiro Maia")
            ? 1
            : 0,
      };
    } else if (string.includes("Capites de Abril")) {
      styleObject = {
        color: "#70c1b3",
        opacity:
          props.selectedRua.length === 0
            ? 1
            : props.selectedRua.includes("Capites de Abril")
            ? 1
            : 0,
      };
    }
    return styleObject;
  }

  data_features.map((street) =>
    street.geometry.coordinates.map((line) => line.reverse())
  );

  const purple = { color: "purple" };

  return (
    <>
      <MapContainer
        center={position}
        zoom={7}
        style={{ width: "100%", textAlign: "center" }}
      >
        {/* layer from list: https://leaflet-extras.github.io/leaflet-providers/preview/ */}
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          url="https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png"
        />
        {data_features.map((el) => {
          return (
            <>
              <Polyline
                pathOptions={style(el.properties.rua)}
                positions={el.geometry.coordinates}
              >
                {" "}
                <Tooltip>
                  <h3>{el.properties.rua}</h3>
                  <p>
                    <span style={{ fontWeight: "bold" }}>Freguesia:</span>{" "}
                    {el.properties.Freguesia}
                  </p>
                  <p>
                    <span style={{ fontWeight: "bold" }}>Concelho:</span>{" "}
                    {el.properties.concelho}
                  </p>
                </Tooltip>
              </Polyline>
            </>
          );
        })}
        {/* <SearchControl
          provider={provider}
          showMarker={false}
          showPopup={false}
          popupFormat={({ query, result }) => result.label}
          maxMarkers={3}
          retainZoomLevel={false}
          animateZoom={true}
          autoClose={true}
          searchLabel={"Escreve uma morada"}
          keepResult={true}
        />{" "} */}
      </MapContainer>
    </>
  );
}
