import Ar from "./examples/qualidade_ar/viz";
import Liberdade from "./examples/liberdade/mapViz";
import Home from "./home";
import "./App.css";
import {
  HashRouter as Router,
  Routes,
  Route,
  Link,
  useLocation,
} from "react-router-dom";
import ReactDOM from "react-dom";
import React from "react";

export default function App() {
  return ReactDOM.render(
    <React.StrictMode>
      <Router basemname={`/${process.env.PUBLIC_URL}`}>
        <div style={{ textAlign: "center" }}>
          {/* A <Routes> looks through its children <Route>s and
              renders the first one that matches the current URL. */}
          <Routes>
            <Route exact path="/Ar" element={<Ar></Ar>}></Route>
            <Route path="/liberdade" element={<Liberdade></Liberdade>}></Route>
            <Route></Route>
            <Route path="/" element={<Home></Home>}></Route>
          </Routes>
          <div style={{ display: "inline-block" }}>
            <p>
              <Link to="/">Home</Link>
            </p>
            <p>
              <Link to="/Ar">Poluio em Portugal</Link>
            </p>
            <p>
              <Link to="/liberdade">Liberdade</Link>
            </p>
          </div>
        </div>
      </Router>
    </React.StrictMode>,
    document.getElementById("root")
  );
}
